nav{
    /* display: none; */
    background: rgba(0,0,0,.3);
    width: max-content;
    display: block;
    padding: .7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    visibility: hidden;

}
nav a{
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;


}
nav a:hover{
    background: rgba(0,0,0,.3);
}

nav >a.active {
    background: var(--color-primary);
    color: var(--color-bg);
    /* color:yellow */

}

@media screen and (max-width:1024px){
 
    nav{
        visibility: visible;
    }
}

/* ===== media queries for smaller divices */
@media screen and (max-width:600px){
    nav{
        visibility: visible;
    }
}